body.active-modal {
    overflow-y: hidden;
}

.btn-modal {
    padding: 5px 5px;
    display: block;
    font-size: 15px;
}

.modal{
    position: absolute;
    left: 50%;
    top: 50%;
    width: 300px;
    height: 300px;
    margin-left: -150px;
    margin-top: -150px;
}

.overlay{
    background: rgba(49,49,49,0.8);
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
}
.modal-content {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    line-height: 1.4;
    background: #f1f1f1;
    padding: 28px 36px;
    border-radius: 16px;
    border-color: green;
    border-style: solid;
    border-width: 5px;
    min-height: 375px;
    min-width: 400px;
    
}

.close-modal {
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 5px 7px;
}

.modal-header{
    text-align: center;
    padding-bottom: 30px;
    
}

.register-input{
    display: flex;
    padding-bottom: 10px;
    justify-content: space-between;
}

.btn-container{
    padding-top: 30px;
    display: flex;
    justify-content: center;
}