.card{
    height: 300px;
    width: 200px;
    margin: 0;
    padding: 0;
    overflow: hidden;
    box-shadow: 0 2px 20px gray;
    border-radius: 6px;
    border-color: rgb(2, 29, 13);
    border-style: solid;
    border-width: 2px;
    display: flex;
    justify-content: center;
    background-color: rgb(12, 68, 12);
    
}

.card-body{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.card-title{
    color: white;
    text-align: center;
    padding-top: 15px;
}

.card-reserve{
    cursor: pointer;
    padding: 5px;
    
}

.reserve-btn-container{
    display: flex;
    justify-content: center;
    
}